  padding: 0;
  height: 80vh;
  width: 100vw;
  display: flex;
  justify-content: center; }

.signup_container {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 90vw;
  max-width: 700px;
  border: solid 0.5px rgba(0, 0, 0, 0.1);
  border-radius: 10px; }
  .signup_container .signup_title {
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 2.1rem;
    padding: 10px;
    border-bottom: solid 2px rgba(0, 0, 0, 0.1); }

.user-card_section {
  display: flex;
  height: 10%;
  margin-bottom: 40px;
  margin-left: 10px; }
  .user-card_section .user-card_column {
    width: 70px; }
  .user-card_section .user-card_title {
    font-weight: 800; }

.userinfo_section {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  margin-top: 30px;
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.8); }
  .userinfo_section .userinfo_wrapper {
    border: solid 0.5px rgba(0, 0, 0, 0.3);
    width: 80%;
    padding: 30px;
    border-radius: 6px; }
    .userinfo_section .userinfo_wrapper .userinfo_title_myinfo {
      border-bottom: solid 1px rgba(0, 0, 0, 0.3);
      font-size: 1.3rem;
      font-weight: 800;
      min-width: 100px;
      width: 18%;
      padding: 20px;
      margin-bottom: 30px; }
    .userinfo_section .userinfo_wrapper .userinfo_title_history {
      border-bottom: solid 1px rgba(0, 0, 0, 0.3);
      font-size: 1.3rem;
      font-weight: 800;
      min-width: 100px;
      width: 100%;
      padding: 20px;
      margin-bottom: 30px; }

