.myorder-card {
  min-width: 150px;
  min-height: 200px;
  width: 60vh;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  margin-top: 20px;
  margin-bottom: 40px;
  border-radius: 15px;
  background-color: #f6f5f0;
  @keyframes rotate {
    0% {
      transform: rotate(0deg);
      background-color: rgba(254, 102, 0, 1);
    }
    50% {
      transform: rotate(180deg);
      background-color: rgba(254, 102, 0, 0.3);
    }
    100% {
      transform: rotate(360deg);
      background-color: rgba(254, 102, 0, 1);
    }
  }
  @keyframes blink {
    0% {
      color: red;
      opacity: 0.2;
    }
    50% {
      color: red;
      opacity: 1;
    }
    100% {
      color: red;
      opacity: 0.2;
    }
  }
  @keyframes finish {
    0% {
      transform: scale(0.8);
      background-color: rgba(254, 102, 0, 1);
    }
    50% {
      transform: scale(1);
      background-color: rgba(254, 102, 0, 0.3);
    }
    100% {
      transform: scale(0.8);
      background-color: rgba(254, 102, 0, 1);
    }
  }

  .myorder-status-ball {
    color: rgba(255, 255, 255, 0.8);
    font-size: 2rem;
    animation: rotate 3s infinite;
    position: relative;
    top: -60px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 250px;
    height: 250px;
    margin-bottom: 0px;
    border-radius: 100%;
  }

  .myorder-status-ball-finish {
    color: rgba(255, 255, 255, 0.8);
    font-size: 2rem;
    animation: finish 3s infinite;
    position: relative;
    top: -60px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 250px;
    height: 250px;
    margin-bottom: 0px;
    border-radius: 100%;
  }

  .myorder-box-making {
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: solid 1px rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  .myorder-status-paid {
    margin-bottom: 10px;
    animation: blink 2s infinite;
  }
}
.myorder-box-made {
  display: flex;
  flex-direction: column;
  align-items: center;
}
