.payform-container {
    width: 60%;
    min-width: 350px;
    min-height: 700px;
    height: 100vh;
    background-color: #f6f5f0;
    margin: 30px;
    padding-top: 5%;
  
    .payform-title {
      justify-content: flex-start;
      font-size: 1.5rem;
      border-bottom: solid 1px rgba(0, 0, 0, 0.8);
      width: 100px;
      display: flex;
      justify-content: center;
      margin-top: 5%;
      margin-left: 10%;
      margin-bottom: 8%;
    }
    .payform {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    .payform-total {
      font-size: 1.3rem;
      display: flex;
      justify-content: space-around;
      width: 80%;
      height: 30px;
      margin-top: 5%;
      padding-top: 30px;
      border-top: solid 1px rgba(0, 0, 0, 0.8);
      div {
        width: 150px;
        height: 50px;
        padding-left: 30px;
      }
      .payform-total-price {
        padding-left: 50px;
      }
    }
    .payform-list {
      width: 100%;
    }
    button {
      margin-top: 15%;
      width: 170px;
      height: 40px;
      font-size: 1.2rem;
    }
  }
  